<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>

      <span class="content__info">
        {{ countProduct }}
        {{ countProduct | declText(['товар', 'товара', 'товаров']) }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products"
              :key="item.product.id"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span> {{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}"
            class="cart__button button button--primery" type="submit"
            :disabled="totalPrice === 0"
          >
            Оформить заказ
          </router-link>

          <router-link
            v-if="totalPrice === 0"
            tag="button"
            :to="{name: 'main'}"
            class="cart__button button button--primery"
          >
            На главную
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartItem from '@/components/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import declTextMixin from '@/mixins/declTextMixin';
import { mapGetters } from 'vuex';

export default {
  components: { CartItem },

  filters: {
    numberFormat,
  },
  mixins: [declTextMixin],
  computed: {
    ...mapGetters('baskets/baskets', {
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      countProduct: 'countProducts',
    }),
  },
};
</script>
