<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info">
        {{ countProductsAll }}
        {{ countProductsAll | declText(['товар', 'товара', 'товаров']) }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        v-bind.sync="filters"
      />

      <section class="catalog">
        <ProductList
          :products="products"
          v-if="!productsLoading &&
          !productsLoadingFailed"
        />

        <Preloader v-if="productsLoading" />

        <div class="error-wrapper" v-if="productsLoadingFailed">
          <h2 class="error-heading">
            Что-то пошло не так
          </h2>
          <button class="error-button" @click.prevent="windowReload">
            перезагрузить
          </button>
        </div>

        <div class="error-wrapper"
          v-if="countProductsAll === 0 && !productsLoading"
        >
          <h2 class="error-heading">
            Пусто
          </h2>
          <button class="error-button"
            @click.prevent="filterCategoryReset"
          >
            Сбросить Фильтр
          </button>
        </div>

        <BasePagination
          v-model="page"
          :count="countProductsAll"
          :per-page="productPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import Preloader from '@/components/Preloader.vue';
import declTextMixin from '@/mixins/declTextMixin';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
    Preloader,
  },
  data() {
    return {
      filters: {
        filterPriceFrom: 1,
        filterPriceTo: 1000000,
        filterCheckedColor: null,
        filterCategoryId: null,
        productProps: [],
      },
      page: 1,
      productPerPage: 3,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    ...mapGetters('global/loadMainProducts', [
      'getProductsData', 'products', 'countProductsAll',
    ]),
  },
  methods: {
    ...mapActions('global/loadMainProducts', ['loadMainProducts']),

    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        this.loadMainProducts({
          categoryId: this.filters.filterCategoryId,
          colorId: this.filters.filterCheckedColor,
          minPrice: this.filters.filterPriceFrom,
          maxPrice: this.filters.filterPriceTo,
          limit: this.productPerPage,
          page: this.page,
          props: this.filters.productProps,
        })
          .catch(() => {
            this.productsLoadingFailed = true;
          })
          .then(() => {
            this.productsLoading = false;
          });
      }, 700);
    },

    filterCategoryReset() {
      this.filters.filterPriceFrom = 1;
      this.filters.filterPriceTo = 1000000;
      this.filters.filterCheckedColor = null;
      this.filters.filterCategoryId = null;
      this.filters.productProps = [];
    },

    windowReload() {
      window.location.href = '/';
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filters: {
      handler() {
        this.loadProducts();
      },
      deep: true,
    },
  },
  mixins: [declTextMixin],
  created() {
    this.loadProducts();
  },
};
</script>
