import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  namespaced: true,
  state: {
    productId: null,
  },
  getters: {
    getProductById(state) {
      return state.productId;
    },
  },
  mutations: {
    updateProductId(state, item) {
      state.productId = item;
    },
  },
  actions: {
    async loadProductById(context, param) {
      await axios.get(`${API_BASE_URL}/api/products/${param}`)
        .then((response) => {
          context.commit('updateProductId', response.data);
        })
        .catch(() => {
          this.$router.push('/404');
        });
    },
  },
};
