<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title" v-if="orderInfo">
        Заказ оформлен <span>№ {{ this.orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;
            деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary" v-if="orderInfo">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.email }}
              </span>
            </li>
          </ul>
        </div>

        <router-link tag="button"
          class="error-button" :to="{name: 'main'}">
          На главную
        </router-link>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order"
              v-for="item in basketInfo"
              :key="item.productOffer.id"
            >
              <h3>{{ item.productOffer.title }}</h3>
              <b>{{ item.productOffer.price | numberFormat }} ₽</b>
              <span>Артикул: {{item.productOffer.id}}</span>
            </li>
          </ul>

          <div class="cart__total" v-if="getOrderInfo">
            <p>Итого:
              <b>{{ getOrderInfo.basket.items.length }}</b>
              {{ getOrderInfo.basket.items.length | declText(['товар', 'товара', 'товаров']) }}
              на сумму <b>{{orderInfo.totalPrice | numberFormat }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import declTextMixin from '@/mixins/declTextMixin';
import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters('order/order', ['getOrderInfo']),

    orderInfo() {
      return this.getOrderInfo;
    },
    basketInfo() {
      return this.getOrderInfo && this.getOrderInfo.basket.items;
    },
  },
  // watch: {
  //   '$route.params.id': {
  //     handler(v) {
  //       this.$store.dispatch('order/order/loadOrderInfo', v)
  //         .catch(() => { this.$router.push('/404'); });
  //     },
  //     immediate: true,
  //   },
  // },
  methods: {
    ...mapActions('order/order', ['loadOrderInfo']),
  },
  filters: {
    numberFormat,
  },
  mixins: [declTextMixin],
  created() {
    if (this.$store.state.order.order.orderInfo
      && this.$store.state.order.order.orderInfo.id === this.$route.params.id) return;

    this.loadOrderInfo(this.$route.params.id);
  },
};
</script>
