import axios from 'axios';
import Qs from 'qs';
import { API_BASE_URL } from '@/config';

export default {
  namespaced: true,
  state: {
    productsData: null,
    productProps: [],
    productItem: null,
  },
  getters: {
    getProductsData(state) {
      return state.productsData;
    },
    products(state, getters) {
      return getters.getProductsData
        ? getters.getProductsData.items.map((product) => ({
          ...product,
          image: product.preview.file.url,
        }))
        : [];
    },
    countProductsAll(state, getters) {
      return getters.getProductsData ? getters.getProductsData.pagination.total : [];
    },
  },
  mutations: {
    syncProductsData(state, data) {
      state.productsData = data;
    },
  },
  actions: {
    async loadMainProducts(context, {
      categoryId,
      colorId,
      minPrice,
      maxPrice,
      limit,
      page,
      props,
    }) {
      await axios.get(`${API_BASE_URL}/api/products`, {
        params: {
          categoryId,
          colorId,
          minPrice,
          maxPrice,
          limit,
          page,
          props,
        },
        /* eslint-disable */
        paramsSerializer: function (params) {
          return Qs.stringify(params, { arrayFormat: 'brackets' });
        },
      })
      .then((response) => {
        context.commit('syncProductsData', response.data)
      })
      .catch((error) => {
        console.log(error);
      });
    },
  },
}
