    <b>
      {{ (item.quantity * item.price) | numberFormat }} ₽
    </b>
    <span>Артикул: {{ item.productOffer.product.id }}</span>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  props: ['item'],
  filters: {
