export default {
  namespaced: true,
  state: {
    productProps: [],
  },
  getters: {
    getProductProps(state) {
      return state.productProps;
    },
  },
  mutations: {
    updateProductProps(state, props) {
      state.productProps.push(props);
    },
    resetProps(state) {
      state.productProps = [];
    },
  },
  actions: {},
};
