<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <picture>
        <img :src="product.image" :alt="product.title" />
      </picture>
    </router-link>

    <template
      v-if="currentCheckedProps"
    >
      <h3 class="catalog__title">
        <router-link
          :to="{ name: 'product', params: { id: product.id } }"
        >
          {{ currentCheckedProps[0] }}
        </router-link>
      </h3>

      <span class="catalog__price">
        {{ currentCheckedProps[1] | numberFormat }} ₽
      </span>
    </template>

    <template v-else>
      <h3 class="catalog__title">
        <router-link
          :to="{ name: 'product', params: { id: product.id } }"
        >
          {{ product.title }}
        </router-link>
      </h3>

      <span class="catalog__price">
        {{ product.price | numberFormat }} ₽
      </span>
    </template>

    <ul class="sizes sizes--primery"
      style="margin-top: 16px"
      v-if="offers && offers.length > 1"
    >
      <li class="sizes__item"
        v-for="offer in offers" :key="offer.id"
      >
        <div
          v-for="prop in offer.propValues" :key="prop.value"
        >
          <label class="sizes__label">
            <input
              class="sizes__radio sr-only"
              type="radio"
              :value="[offer.title, offer.price, offer.id]"
              v-model="propsChosen"
            />
            <span class="sizes__value"> {{ prop.value }} </span>
          </label>
        </div>
      </li>
    </ul>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapMutations } from 'vuex';

export default {
  props: ['product', 'offers'],
  data() {
    return {
      currentCheckedProps: null,
    };
  },

  filters: {
    numberFormat,
  },
  methods: {
    ...mapMutations('global/productProps', ['updateProductProps']),
  },
  computed: {
    propsChosen: {
      get() {
        return this.currentCheckedProps;
      },
      set(value) {
        const currentProps = {
          id: value[2],
          price: value[1],
          title: value[0],
        };
        this.updateProductProps(currentProps);
        this.currentCheckedProps = value;
      },
    },
    ...mapGetters('global/productProps', ['getProductProps']),
  },
};
</script>
