<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.productOffer.product.preview.file.url"
           width="120" height="120"
           :alt="item.title"
      >
    </div>

    <h3 class="product__title">
      {{ item.productOffer.title }}
    </h3>

    <span class="product__code">
      Артикул: {{ item.productOffer.product.id }}
    </span>

    <Counter :amount.sync="amountItem" />

    <b class="product__price">
      {{ (amountItem * item.productOffer.price) | numberFormat }} ₽
    </b>

    <button
      class="product__del button-del" type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import Counter from '@/components/Counter.vue';
import { mapActions } from 'vuex';

export default {
  components: { Counter },
  filters: {
    numberFormat,
  },

  props: ['item'],
  computed: {
    amountItem: {
      get() {
        return this.item.quantity;
      },
      set(value) {
        return this.updateCartProductAmount({
          basketItemId: String(this.item.id),
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions('baskets/baskets', [
      'updateCartProductAmount', 'deleteProductFromCart',
    ]),
    deleteProduct() {
      this.deleteProductFromCart({
        basketItemId: String(this.item.id),
      });
    },
  },
};
</script>
