import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  namespaced: true,
  state: {
    cartProducts: [],
    cartProductsData: [],
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProductsData.map((item) => {
        const { product } = state.cartProductsData.find(
          (p) => p.productOffer.product.id === item.productOffer.product.id,
        );
        return {
          ...item,
          product: {
            ...product,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => (item.price * +item.quantity) + acc, 0,
      );
    },
    countProducts(state, getters) {
      return getters.cartDetailProducts.length;
    },
  },
  mutations: {
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, productsData) {
      state.cartProductsData = productsData;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.id,
        amount: item.quantity,
      }));
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const items = state.cartProducts.find((item) => item.productId === productId);

      if (items) {
        items.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((
        item,
      ) => item.productId !== productId);
    },
  },
  actions: {
    loadCart(context) {
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    addProductToCart(context, { productId, colorId, amount }) {
      return (new Promise((resolve) => setTimeout(resolve, 1000)))
        .then(() => axios.post(`${API_BASE_URL}/api/baskets/products`, {
          productOfferId: productId,
          colorId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((response) => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          }));
    },
    updateCartProductAmount(context, { basketItemId, amount }) {
      context.commit('updateCartProductAmount', { basketItemId, amount });

      if (amount < 1) {
        return;
      }

      axios.put(`${API_BASE_URL}/api/baskets/products`, {
        basketItemId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteProductFromCart(context, { basketItemId }) {
      context.commit('deleteCartProduct', basketItemId);
      return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
        data: {
          basketItemId,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
  },
};
