<template>
  <div style="height: 55vh; position: relative" v-if="productLoading">
    <Preloader />
  </div>

  <div class="error-wrapper" style="height: 55vh" v-else-if="productLoadingField">
    <h2 class="error-heading">
      Ой!<br> Что-то пошло не так
    </h2>
    <button class="error-button" @click.prevent="order">
      перезагрузить
    </button>
  </div>

  <main class="content container" v-else>
    <div class="content__top">
    <ul class="breadcrumbs">
      <li class="breadcrumbs__item">
        <router-link class="breadcrumbs__link" :to="{name: 'main'}">
          Каталог
        </router-link>
      </li>
      <li class="breadcrumbs__item">
        <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
          Корзина
        </router-link>
      </li>
      <li class="breadcrumbs__item">
        <a class="breadcrumbs__link">
          Оформление заказа
        </a>
      </li>
    </ul>

    <h1 class="content__title">
      Корзина
    </h1>
    <span class="content__info">
        {{ countProduct }}
        {{ countProduct | declText(['товар', 'товара', 'товаров']) }}
      </span>
  </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
      <div class="cart__field">
        <div class="cart__data">
          <BaseFormText
            v-model="formData.name"
            :error="formError.name"
            title="ФИО"
            placeholder="Введите ваше полное имя"
          />

          <BaseFormText
            v-model="formData.address"
            :error="formError.address"
            title="Адрес доставки"
            placeholder="Введите ваш адрес"
          />

          <BaseFormText
            v-model="formData.phone"
            :error="formError.phone"
            title="Телефон"
            type="tel"
            placeholder="Введите ваш телефон"
          />

          <BaseFormText
            v-model="formData.email"
            :error="formError.email"
            title="Email"
            type="email"
            placeholder="Введи ваш Email"
          />

          <BaseFormTextarea
            v-model="formData.comment"
            :error="formError.comment"
            title="Комментарий к заказу"
            placeholder="Ваши пожелания"
          />
        </div>
      </div>

      <div class="cart__block">
        <ul class="cart__orders">
          <OrderItem v-for="item in products" :key="item.id" :item="item" />
        </ul>

        <div class="cart__total">
          <p>Итого:
            <b>{{ countProduct }}</b>
            {{ countProduct | declText(['товар', 'товара', 'товаров']) }}
            на сумму <b>{{ totalPrice | numberFormat }} ₽</b>
          </p>
        </div>

        <button class="cart__button button button--primery" type="submit">
          Оформить заказ
        </button>
      </div>
      <div class="cart__error form__error-block" v-if="formErrorMassage">
        <h4>Заявка не отправлена!</h4>
        <p>
          {{ this.formErrorMassage }}
        </p>
      </div>
    </form>
  </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderItem from '@/components/OrderItem.vue';
import numberFormat from '@/helpers/numberFormat';
import declTextMixin from '@/mixins/declTextMixin';
import { mapActions, mapGetters, mapMutations } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import Preloader from '@/components/Preloader.vue';

export default {
  components: {
    BaseFormText, BaseFormTextarea, OrderItem, Preloader,
  },

  data() {
    return {
      formData: {
        deliveryTypeId: 1,
        paymentTypeId: 1,
      },
      formError: {},
      formErrorMassage: '',
      productLoading: false,
      productLoadingField: false,
    };
  },
  filters: {
    numberFormat,
  },
  mixins: [declTextMixin],
  computed: {
    ...mapGetters('baskets/baskets', {
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      countProduct: 'countProducts',
    }),
  },
  methods: {
    ...mapMutations('baskets/baskets', ['resetCart']),
    ...mapActions('order/order', ['makeOrder']),
    order() {
      this.formError = {};
      this.formErrorMassage = '';
      axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.baskets.baskets.userAccessKey,
        },
      })
        .then((response) => {
          this.productLoading = false;
          this.resetCart();
          this.$store.commit('order/order/updateOrderInfo', response.data);
          this.$router.push({
            name: 'orderInfo',
            params: { id: response.data.id },
          });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMassage = error.response.data.error.message;
        });
    },
  },
};
</script>
